










import {Component, Vue} from "vue-property-decorator"
import Portal from "@/managers/portal"
import NotificationMessage from "@/components/NotificationMessage.vue"

@Component({ components: { NotificationMessage } })
export default class RedeemVoucher extends Vue {
	private coupon: string = ""
	private receivedVouchers: number | null = null
	private notifyMsg: string | null = null
	private redeemVoucher(): void {
		Portal.client.call.payments.applyStripeCoupon(this.coupon)
			.response
			.then(res => {
				this.coupon = ""
				this.notifyMsg = `Coupon Code applied successfully`
			})
			.catch(err =>
				this.notifyMsg = err
				)
	}
}
